<template>
  <b-container fluid style="min-width:1150px">
    <b-row>
      <!-- 头部 -->
      <b-navbar toggleable="md" >
        <b-col cols="5" sm="5" md="5">
          <b-navbar-brand href="#/">
            <img src="/images/logo.png" height="58" alt="鹍远基因" style="vertical-align: top;">
          </b-navbar-brand>
          <b-link @click="toggleAside">
            <img src="/images/smallnav.png" width="26" height="26" alt="">
          </b-link>
        </b-col>
        <b-col cols="7" sm="7" md="7" style="display:flex;height:60px">
          <!--<h5 style="line-height:60px;">大健康管理系统</h5>-->
          <b-navbar-nav class="ml-auto" style="margin-right:30px;">
            <b-nav-item-dropdown right>
              <template slot="button-content">
                <em>{{userName}}</em>
              </template>
              <b-dropdown-item href="#">个人资料</b-dropdown-item>
              <b-dropdown-item @click="editPwd(true)" v-if="role!=4">修改密码</b-dropdown-item>
              <b-dropdown-item @click="logout">退出</b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>
        </b-col>
      </b-navbar>
      <!-- 修改密码 -->
      <b-modal ref="editPwd" hide-footer title="修改密码" :no-close-on-backdrop="true" :no-close-on-esc="true">
        <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event)">
          <b-form-group id="oldPwdGroup" label="旧密码:" label-size="sm" label-for="oldPwd">
            <b-form-input autocomplete="off" size="sm" type="password" id="oldPwd" :state="oldState" v-model.trim="pwdForm.oldPwd" required>
            </b-form-input>
          </b-form-group>
          <b-form-group id="newPwdGroup" label="新密码:" label-size="sm" label-for="newPwd">
            <b-form-input autocomplete="off" size="sm" type="password" id="newPwd" :state="newState" v-model.trim="pwdForm.newPwd" required>
            </b-form-input>
          </b-form-group>
          <b-form-group id="reNewPwdGroup" label="再次输入新密码:" label-size="sm" label-for="reNewPwd">
            <b-form-input autocomplete="off" size="sm" type="password" id="reNewPwd" :state="reNewState" v-model.trim="pwdForm.reNewPwd" required aria-describedby="inputLiveFeedback">
            </b-form-input>
            <b-form-invalid-feedback id="inputLiveFeedback">
              两次新密码输入不一致
            </b-form-invalid-feedback>
          </b-form-group>
          <b-button type="submit" style="width:100%" variant="info" :disabled="pwdForm.oldPwd&&pwdForm.newPwd&&pwdForm.reNewPwd&&!newState&&!reNewState">修改</b-button>
        </b-form>
      </b-modal>
    </b-row>
    <b-row>
      <b-col cols="2" sm="2" md="2" class="asideCon" v-show="asideToggle">
        <!-- 侧边栏 -->
        <aside id="asideMenu">
          <!-- <Sider> -->
            <Menu ref="sideMenu" id="backMeun" :active-name="active" width="auto" :open-names="openNames" @on-select="handleSelect">
              <MenuItem data-name="authority" name="authority" v-if="role=='4'">
                <span>权限分配</span>
              </MenuItem>
              <Submenu name="sopManage" v-if="role=='4'">
                <template slot="title">
                  SOP管理
                </template>
                <MenuItem data-name="bloodsop" name="bloodsop">血浆分离SOP</MenuItem>
                <MenuItem data-name="nucleicsop" name="nucleicsop">核酸提取SOP</MenuItem>
                <MenuItem data-name="libqcasop" name="libqcasop">文库定性质控SOP</MenuItem>
                <MenuItem data-name="libqcbsop" name="libqcbsop">文库定量质控SOP</MenuItem>
                <MenuItem data-name="libqpcrsop" name="libqpcrsop">文库qPCR SOP</MenuItem>
                <MenuItem data-name="seqsop" name="seqsop">上机方案SOP</MenuItem>
              </Submenu>
              <Submenu name="labSupplyManage" v-if="role=='4'">
                <template slot="title">
                  实验用品管理
                </template>
                <MenuItem data-name="kit" name="kit">试剂盒管理</MenuItem>
              </Submenu>
              <MenuItem data-name="barcode" name="barcode" v-if="role=='4'">
                <span>Barcode管理</span>
              </MenuItem>
              <MenuItem data-name="seqplatform" name="seqplatform" v-if="role=='4'">
                <span>测序平台管理</span>
              </MenuItem>
              <Submenu name="subjectManage" v-if="role=='3'&&(permissionList.indexOf('1')>-1?true:false)">
                <template slot="title">
                  受检者管理
                </template>
                <MenuItem data-name="addsubject" name="addsubject" v-if="permissionList.indexOf('1')>-1?true:false">添加受检者</MenuItem>
                <MenuItem data-name="mysubject" name="mysubject" v-if="permissionList.indexOf('1')>-1?true:false">我的受检者</MenuItem>
              </Submenu>
              <Submenu name="reviewManage" v-if="role=='3'&&['2','4','7','11','16','20','23','32'].find(item=>permissionList.includes(item))">
                <template slot="title">
                  审批管理
                </template>
                <MenuItem data-name="revieworder" name="revieworder" v-if="permissionList.indexOf('2')>-1?true:false">订单审批</MenuItem>
                <MenuItem data-name="reviewsample" name="reviewsample" v-if="permissionList.indexOf('4')>-1?true:false">样本审批</MenuItem>
                <MenuItem data-name="reviewblood" name="reviewblood" v-if="permissionList.indexOf('7')>-1?true:false">血浆审批</MenuItem>
                <MenuItem data-name="reviewnucleic" name="reviewnucleic" v-if="permissionList.indexOf('11')>-1?true:false">核酸审批</MenuItem>
                <MenuItem data-name="reviewlibrary" name="reviewlibrary" v-if="permissionList.indexOf('16')>-1?true:false">文库审批</MenuItem>
                <MenuItem data-name="reviewsequence" name="reviewsequence" v-if="permissionList.indexOf('20')>-1?true:false">分析审批</MenuItem>
                <!-- <MenuItem data-name="authority" name="reviewqc">QC审批</MenuItem> -->
                <MenuItem data-name="reviewreport" name="reviewreport" v-if="permissionList.indexOf('23')>-1?true:false">报告一级审批</MenuItem>
                <MenuItem data-name="reviewreport2" name="reviewreport2" v-if="permissionList.indexOf('32')>-1?true:false">报告二级审批</MenuItem>
              </Submenu>
              <Submenu name="qcManage" v-if="role=='3'&&['6','9','7','10','13','15','14'].find(item=>permissionList.includes(item))">
                <template slot="title">
                  QC管理
                </template>
                <MenuItem data-name="plasmqc" name="plasmqc" v-if="permissionList.indexOf('6')>-1?true:false">血浆质控</MenuItem>
                <MenuItem data-name="naqca" name="naqca" v-if="permissionList.indexOf('9')>-1?true:false">核酸定性质控</MenuItem>
                <MenuItem data-name="naqcb" name="naqcb" v-if="permissionList.indexOf('10')>-1?true:false">核酸定量质控</MenuItem>
                <MenuItem data-name="libqca" name="libqca" v-if="permissionList.indexOf('13')>-1?true:false">文库定性质控</MenuItem>
                <MenuItem data-name="libqcb" name="libqcb" v-if="permissionList.indexOf('15')>-1?true:false">文库定量质控</MenuItem>
                <MenuItem data-name="libqpcr" name="libqpcr" v-if="permissionList.indexOf('14')>-1?true:false">文库qPCR定量质控</MenuItem>
              </Submenu>
              <MenuItem data-name="sample" name="sample" v-if="role=='3'&&(permissionList.indexOf('3')>-1?true:false)">
                <span>样本管理</span>
              </MenuItem>
              <MenuItem data-name="blood" name="blood" v-if="role=='3'&&(permissionList.indexOf('5')>-1?true:false)">
                <span>血浆管理</span>
              </MenuItem>
              <MenuItem data-name="nucleic" name="nucleic" v-if="role=='3'&&['8','9','10'].find(item=>permissionList.includes(item))">
                <span>核酸管理</span>
              </MenuItem>
              <MenuItem data-name="library" name="library" v-if="role=='3'&&['12','13','14','15'].find(item=>permissionList.includes(item))">
                <span>文库管理</span>
              </MenuItem>
              <MenuItem data-name="sequence" name="sequence" v-if="role=='3'&&['19','21'].find(item=>permissionList.includes(item))">
                <span>测序文件管理</span>
              </MenuItem>
              <MenuItem data-name="queue" name="queue" v-if="role=='3'&&(permissionList.indexOf('31')>-1?true:false)">
                <span>测序队列管理</span>
              </MenuItem>
              <MenuItem data-name="sendmail" name="sendmail" v-if="role=='3'&&(permissionList.indexOf('33')>-1?true:false)">
                <span>报告推送服务</span>
              </MenuItem>
              <MenuItem data-name="mail" name="mail" v-if="role=='3'&&(permissionList.indexOf('34')>-1?true:false)">
                <span>邮箱配置管理</span>
              </MenuItem>
              <MenuItem data-name="report" name="report" v-if="role=='3'&&(permissionList.indexOf('24')>-1?true:false)">
                <span>报告管理</span>
              </MenuItem>
              <Submenu name="statisticManage" v-if="role=='3'&&(permissionList.indexOf('30')>-1?true:false)">
                <template slot="title">
                  统计管理
                </template>
                <MenuItem data-name="overview" name="overview">总览</MenuItem>
                <!-- <MenuItem data-name="testresult" name="testresult">检验结果</MenuItem>
                <MenuItem data-name="sampleinfo" name="sampleinfo">样本信息</MenuItem>
                <MenuItem data-name="detectqcindicators" name="detectqcindicators">检测QC指标</MenuItem>
                <MenuItem data-name="detectioncycle" name="detectioncycle">检测周期</MenuItem>
                <MenuItem data-name="detectstaffworkload" name="detectstaffworkload">检测人员工作量</MenuItem>-->
              </Submenu>
            </Menu>
          <!-- </Sider> -->
        </aside>
      </b-col>
      <!-- 内容 -->
      <b-col :cols="asideToggle?10:12" :sm="asideToggle?10:12" :md="asideToggle?10:12" class="sectionCon">
        <div id="toggleSection">
          <router-view></router-view>
        </div>
        <!-- 尾部 -->
        <footer>Singlera Genomic Inc | Copyright 2018 </footer>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
  export default{
    mounted(){
      // 当浏览器窗口改变时更改组件大小
      if($("#asideMenu").length!==0&&$("#toggleSection").length!==0){
        window.onresize=function windowResize(){
          $("#asideMenu").get(0).style.minHeight=window.innerHeight-60+"px" 
          $("#toggleSection").get(0).style.minHeight=window.innerHeight-60+"px" 
        }
      }
      $("#asideMenu").get(0).style.minHeight=window.innerHeight-60+"px"
      $("#toggleSection").get(0).style.minHeight=window.innerHeight-60+"px"
      
      // 判断当前用户角色
      this.role = this.$cookies.get("role")
      // 获取当前用户的email
      if(this.role!=='1'&&this.$cookies.get('Authorization')){
        this.loginBtnShow = false
        if(this.$cookies.get('email')){
          this.userName = this.$cookies.get('email')
        }else{
          // 如果email在cookie不存在，就发送请求获取用户信息
          let obj = {}
          obj.Authorization = this.$cookies.get("Authorization")
          this.$http.post("api/get-details",qs.stringify(obj)).then(response=>{
            // console.log(response.data)
            if(response.data.success.email){
              this.userName = response.data.success.email
              this.$cookies.set('email',response.data.success.email,60*60*24)
            }
          })
        }
      }else{
        this.loginBtnShow = true
        this.$router.push("/login")
      }
      // 判断当前用户的权限,并转成数组
      if(this.$cookies.get("permissionvue")){
        this.permissionList = this.$cookies.get("permissionvue").split(',')
        this.$nextTick(()=>{
          // console.log(this.$route)
          if(this.$route.fullPath==='/back'){
            // 判断当前用户需要显示的第一个菜单，获取MenuItem
            let path = $("#backMeun .ivu-menu-item").eq(0).attr('data-name')
            // console.log(path);
            // 根据当前页面的hash判断哪个菜单栏高亮
            this.active = path
            this.highSider()
            this.$router.push("/"+path)
          }else{
            // 根据当前页面的hash判断哪个菜单栏高亮
            // console.log(location.hash)
            this.active = location.hash.split("/")[1]
            this.highSider()
          }
          
        })
      }
    },
    data(){
      return{
        userName:'',
        active:location.hash.split("/")[1],
        role:'',
        permissionList:[],
        openNames:[],
        asideToggle:true,
        // newState:null,
        // reNewState:null,
        oldState:null,
        pwdForm:{
          oldPwd:'',
          newPwd:'',
          reNewPwd:''
        }
      }
    },
    computed:{
      newState(){
        if(this.pwdForm.newPwd&&this.pwdForm.newPwd===this.pwdForm.oldPwd){
          this.$Message.error("新旧密码不能相同！")
          return false
        }else if(this.pwdForm.newPwd===this.pwdForm.reNewPwd){
          return true
        }else{
          return false
        }
      },
      reNewState(){
        if(this.pwdForm.newPwd===this.pwdForm.reNewPwd){
          return true
        }else{
          return false
        }
      }
    },
    methods:{
      toggleAside(){
        this.asideToggle=!this.asideToggle
        $("#toggleSection").get(0).style.minHeight=window.innerHeight-60+"px" 
      },
      editPwd(){
        this.$refs.editPwd.show()
      },
      onSubmit(evt){
        this.$Loading.start()
        // 修改完密码后，应该让用户重新登录，并删除相关cookie
        this.$http.post("/api/changepwd",qs.stringify({oldpassword:this.pwdForm.oldPwd,newpassword:this.pwdForm.newPwd})).then(response=>{
          if(response.data.success){
            this.oldState = null
            this.$cookies.remove("Authorization")
            this.$cookies.remove("permissionvue")
            this.$cookies.remove("id")
            this.$cookies.remove("role")
            this.$cookies.remove("pwd")
            this.$router.push("/login")
          }
          if(response.data.error==="当前密码错误"){
            this.oldState = false
          }
          this.$Loading.finish()
        })
        
      },
      logout(){
        this.$Loading.start()
        this.$http.post("/api/logout").then(response=>{
          // console.log(response.data)
          if(response.data.success){
            this.$cookies.remove("Authorization")
            this.$cookies.remove("permissionvue")
            this.$cookies.remove("id")
            this.$cookies.remove("role")
            this.$router.push("/login")
            this.$Loading.finish()
          }
        })
      },
      handleSelect(name){
        // console.log(name)
        this.active=name
        window.scrollTo(0,0)
        this.$router.push("/" + name)

      },
      highSider(){
        switch (this.active){
          case "addsubject":
            this.openNames=["subjectManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "mysubject":
            this.openNames=["subjectManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          // 审批管理
          case "revieworder":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewsample":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewblood":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewnucleic":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewlibrary":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewsequence":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewqc":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewreport":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "reviewreport2":
            this.openNames=["reviewManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          // sop管理
          case "bloodsop":
            this.openNames=["sopManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "nucleicsop":
            this.openNames=["sopManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "libqcasop":
            this.openNames=["sopManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "libqcbsop":
            this.openNames=["sopManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "libqpcrsop":
            this.openNames=["sopManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "seqsop":
            this.openNames=["sopManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          // 实验用品管理
          case "kit":
            this.openNames=["labSupplyManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          // qc管理
          case "plasmqc":
            this.openNames=["qcManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "naqca":
            this.openNames=["qcManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "naqcb":
            this.openNames=["qcManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "libqca":
            this.openNames=["qcManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "libqcb":
            this.openNames=["qcManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "libqpcr":
            this.openNames=["qcManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "libqpcr":
            this.openNames=["statisticManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "overview":
            this.openNames=["statisticManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "testresult":
            this.openNames=["statisticManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "sampleinfo":
            this.openNames=["statisticManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "detectqcindicators":
            this.openNames=["statisticManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "detectioncycle":
            this.openNames=["statisticManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          case "detectstaffworkload":
            this.openNames=["statisticManage"]
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
          break;
          default:
            this.$nextTick(function() {
              this.$refs.sideMenu.updateOpened();
              this.$refs.sideMenu.updateActiveName();
            })
        }
      }
    }
  }
</script>
<style lang='less' scoped>
@import '../../less/init.less';
.navbar{
  width:100%;
  height: 60px;
  line-height: 60px;
  padding:0;
  box-sizing:border-box;
  border-bottom:1px solid #eee;
  .navbar-brand{
    padding-top:0;
    padding-bottom:0;
    margin-right: 50px;
  }
}
.asideCon{
  position: relative;
  min-width: 190px;
  padding-right: 0;
  padding-left: 0;
  background-color: @themeColor;
  #asideMenu{
    box-sizing: border-box;
    padding-top:100px;
    padding-bottom:50px;
  }
}
.sectionCon{
  padding-right: 0;
  padding-left: 0;
  #toggleSection{
    min-width: 910px;
    padding-bottom: 30px;
  }
  footer{
    width: 100%;
    height: 25px;
    line-height: 25px;
    font-size: 12px;
    text-align: center;
    background-color: #f9f9f9;
    position: absolute;
    left: 0;
    bottom: 0;
  }
}
</style>


